/* General Profile Container Styles */
.profile-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #f4f4f4;
    padding: 40px;
    min-height: 100vh; /* Full height to center content vertically */
  }
  
  /* Profile Header (Name) */
  .profile-header {
    text-align: center;
    margin-bottom: 20px; /* Space between name and image */
  }
  
  .profile-header h1 {
    font-size: 2.5rem;
    color: #333;
    font-weight: bold;
  }
  
  /* Profile Image Styles */
  .profile-image {
    border-radius: 50%;
    width: 150px;
    height: 150px;
    object-fit: cover;
    border: 5px solid #333; /* Optional border around the headshot */
    margin-bottom: 20px; /* Space between image and description */
  }
  
  /* Profile Description Section */
  .profile-description {
    max-width: 800px;
    padding: 20px;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .profile-description p {
    font-size: 1.1rem;
    color: #555;
    line-height: 1.6;
    margin-bottom: 20px;
  }
  
  .profile-description h2 {
    font-size: 1.8rem;
    color: #333;
    margin-bottom: 10px;
  }
  
  /* Responsive Design for smaller screens */
  @media (max-width: 768px) {
    .profile-container {
      padding: 20px;
    }
  
    .profile-header h1 {
      font-size: 2rem;
    }
  
    .profile-description {
      padding: 15px;
    }
  }
  
  
  
  