.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #333;
  padding: 10px;
}

.nav-links {
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0;
}

.nav-links li {
  margin: 0 15px; /* Space out each nav item */
  position: relative; /* Required for positioning dropdown */
}

.nav-links a {
  color: #fff;
  text-decoration: none;
  font-size: 16px;
}

.nav-links li:hover {
  cursor: pointer;
}

.dropdown {
  position: relative; /* Ensure dropdown works correctly within the flex layout */
}

.dropdown-text {
  color: #fff;
  font-size: 16px;
  padding: 0px 7px 20px 15px;
  display: inline-block; /* Ensure it aligns with other items */
}

.dropdown-menu {
  position: absolute;
  top: 100%;
  left: 0;
  background-color: #444;
  list-style: none;
  padding: 10px;
  margin: 0;
  display: block;
  width: 200px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.dropdown-menu li {
  padding: 8px 10px;
}

.dropdown-item {
  color: #fff;
  text-decoration: none;
  display: block;
}

.dropdown-item:hover {
  background-color: #555;
}

.dropdown:hover .dropdown-menu {
  visibility: visible;
  opacity: 1;
}



