/* Reset default browser styles */
{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  }
  
  /* Set the base font and background color */
  body {
    font-family: 'Arial', sans-serif;
    font-size: 16px;
    line-height: 1.5;
    background-color: #f9f9f9;
    color: #333;
  }
  
  /* Global link styles */
  a {
    color: #007BFF;
    text-decoration: none;
  }
  
  a:hover {
    text-decoration: underline;
  }
  
  /* Add a container class for layout consistency */
  .container {
    width: 90%;
    max-width: 1200px;
    margin: 0 auto;
  }
  
  /* Add a global styling for headings */
  h1, h2, h3, h4, h5, h6 {
    font-weight: bold;
    margin-bottom: 10px;
  }
  
  /* Add spacing between sections */
  section {
    margin: 20px 0;
  }